html {
  scroll-behavior: smooth;
}

body {
  background-color: #212121;
  color: #f8f8f8;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #f8f8f869;

  a {
    color: #f8f8f8;
    text-decoration: none;
    &:hover {
      font-weight: bold;
      text-decoration: underline;
    }
  }

  .logo {
    margin-left: 3%;
    width: 5%;
    img {
      width: 100%;
    }
  }
}

.menu {
  width: 50%;
  ul {
    list-style: none;
    display: flex;
    justify-content: space-around;
  }
}

.perfilImg {
  img {
    width: 152px;
    height: 321px;
    box-shadow: 0 0 8px #f8f8f8;
  }
}

main {
  border: 3px solid #f8f8f8;
  border-radius: 10px;
  box-shadow: 0 0 8px #f8f8f8;
  margin: 5%;
  padding: 3% 0 2% 0;

  .personal-info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 10%;
  }

  .social-media {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 2% 10% 0 10%;
    .logos {
      width: 15%;
      img {
        margin: 0 3%;
        width: 20px;
        height: 20px;
      }
    }
  }
}


.whatIDo {
  margin: 5%;
  .services-content {
    display: flex;
    .back-end {
      margin-left: 5%;
    }
    .individual-service {
      width: 25%;
      background-color: #f8f8f8;
      color: #212121;
      border-radius: 10px;
      padding: 10px;
      img {
        width: 40px;
        height: 40px;
      }
    }
  }
}

.skills-container {
  margin: 5%;
  width: 90%;
  .skills-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    list-style: none;
    li {
      display: flex;
      align-items: center;
      margin-right: 25%;
      flex: 1;
      img {
        width: 50%;
        margin-right: 10%;
        margin-bottom: 10%;
      }
    }
  }
}

.experience-container {
  margin: 5%;
  text-align: justify;

  .experience {
    border-bottom: 2px solid #f8f8f8;

    &:nth-last-child(1) {
      border-bottom: none;
    }
  }
}



//FORM INIT-----------------------------------
form {
  border: 2px solid #F8F8F8;
  padding: 20px;
  border-radius: 5px;
  width: 40%;
  margin: 0 auto;
}

label {
  display: block;
  margin-bottom: 10px;
}

input[type="text"], textarea {
  width: 95%;
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid #F8F8F8;
  border-radius: 5px;
}

textarea {
  max-width: 95%;
  min-width: 95%;
}

button[type="submit"] {
  padding: 10px 20px;
  border: 2px solid #f8f8f8;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
}

button[type="submit"]:hover {
  background-color: #212121;
  color: #f8f8f8;
  border: 2px solid #f8f8f8;
}
//FORM FINISH-----------------------------------


.contact-container {
  margin-left: 5%;

  .contact-content {
    display: flex;
  }
}

.p-footer {
  text-align: center;
  border-top: 1px solid #f8f8f881;
  padding-top: 15px;
}